.logo-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  box-sizing: border-box;
  border-radius: 100%;
  background-color: rgba(230, 246, 255, 0.9);
  width: 36vh;
  height: 36vh;
  box-shadow: 3px 10px 5px 3px rgba(2, 45, 82, 0.2);
}
.logo-img {
  display: block;
  width: 80%;
  /* height: 25vh; */
  transition: 0.5s;
}
