.container {
  width: 100%;
  height: 100vh;
  background-color: #001930;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Gowun Dodum", sans-serif;
}

.inner {
  width: 80%;
  height: 70%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  /* border: 1px solid #000; */
  color: white;
}

.header-text {
  font-size: 2.2rem;
  margin-bottom: 3%;
  opacity: 0;
  transform: translate(0, 20px);

  animation-name: floating;
  animation-delay: 0.1s;
  animation-fill-mode: forwards;
  animation-duration: 1.2s;
}

@keyframes floating {
  to {
    transform: translate(0, -10px);
    opacity: 1;
  }
}

.chart-wrap {
  display: flex;
  justify-content: center;
  /* align-items: flex-end; */
}

.chart {
  width: 70%;
  /* border: 1px solid #ffffff; */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.chart img {
  width: 100%;
  border: 4px solid #ffffff;
  border-radius: 20px;
  box-shadow: -10px 10px 10px 2px rgba(255, 255, 255, 0.5),
    -2px 0px 0px 0px #888;
    
    opacity: 0;
    animation-name: tackle;
    animation-delay: 1s;
    animation-fill-mode: forwards;
    animation-duration: 1.5s;
  }
  @keyframes tackle {
    to {
      transform: translate(0, 0);
      opacity: 1;
    }
  }

.close-btn {
  border: 1px solid #000;
  padding: 1%;
}

.stretching-timeset {
}

.stretching-timeset h1 {
  font-size: 1.5rem;
}

.stretching-timeset span {
  margin-top: 30px;
  border: 1px solid #000;
  display: inline-block;
  padding: 5%;
  font-size: 1.5rem;
}

.right-box {
  width: 50%;
  text-align: center;
}

.mode-wrap {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  width: 80%;
  /* border: 1px solid #000; */
  height: 70vh;
  background-color: #fff;
  padding-bottom: 10%;
}

.silent-mode {
  width: 100%;
}

.mission-mode {
  width: 100%;
}

.inner-box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 0 auto;
  align-items: center;
  border: 1px solid #000;
  width: 80%;
  height: 90%;
  background-color: #eeeeee;
}

.inner-box p {
  font-size: 1rem;
}

.go-down {
  box-sizing: border-box;
  padding: 20px;
  position: absolute;
  top: 585%;
  /* background-color: #0084ff; */
  transform: translate(0, 0px);

  /* animation-name: tongtong;
  animation-duration: 0.6s;
  animation-iteration-count: infinite;
  animation-direction: alternate; */
}

.go-down-icon {
  width: 50px;
  height: 50px;
  color: rgba(255, 255, 255, 0.5);
}

.go-down:hover {
  cursor: pointer;
}

@keyframes tongtong {
  to {
    transform: translate(0, -10px) scale(100%);
  }
}
