.canvas-container {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  outline: none;
  position: fixed;
  z-index: -10;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.canvas-container::-webkit-scrollbar {
  display: none;
}
