
.MyCalendar {
  display: flex;
  justify-content: center;
  margin-top: 10%;
}

.calender-container {
  display: flex;
  width: 100%;
  justify-content: center;
  /* padding: 0 5% 0 5%; */
  /* border: 1px solid #ff3f3f; */
}

/* 캘린더 wrap (배경) */
.react-calendar {
  width: 40%;
  height: 70%;
  padding: 20px;
  background-color: rgb(223, 242, 255);
  /* border: 1px solid #000; */
  border-radius: 30px;
  /* border: 5px solid #868db3; */
  color: #222;
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.125em;
  box-shadow: 5px 5px 5px 5px rgba(34, 93, 141, 0.8);
  margin-right: 10%;
  margin-top: 8%;
}

.react-calendar--doubleView {
  width: 700px;
}
.react-calendar--doubleView .react-calendar__viewContainer {
  display: flex;
  margin: -0.5em;
}
.react-calendar--doubleView .react-calendar__viewContainer > * {
  width: 50%;
  margin: 0.5em;
}
.react-calendar,
.react-calendar *,
.react-calendar *:before,
.react-calendar *:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

/* 각 일자 디자인 */
.react-calendar button {
  margin: 0;
  border: 0;
  outline: none;
  background-color: rgb(223, 242, 255);
  /* border: 1px solid #000; */
}
.react-calendar button:enabled:hover {
  cursor: pointer;
}

.react-calendar__navigation {
  display: flex;
  height: 44px;
  margin-bottom: 1em;
}

/* 캘린더 네비 */
.react-calendar__navigation button {
  color: #18344e;
  min-width: 44px;
  background: none;
  font-size: 16px;
  margin-top: 8px;
  font-weight: bold;
}

/* 캘린더 네비 화살표 */
.react-calendar__navigation button:enabled:hover,
.react-calendar__navigation button:enabled:focus {
  /* background-color: #ffffff; */
  /* border: 1px solid #000; */
}

/* 월 화 수 목 금 토 일 */
.react-calendar__month-view__weekdays {
  text-align: center;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 0.75em;
  margin-bottom: 2%;
}

/* 요일 밑의 밑줄 지우기 */
abbr[title] {
  text-decoration: none;
}

/* 머임 */
.react-calendar__tile {
  max-width: 100%;
  padding: 10px 6.6667px;
  border-radius: 9999px;
  text-align: center;
  line-height: 16px;
}

/* 일자 호버 */
.react-calendar__tile:enabled:hover,
.react-calendar__tile:enabled:focus {
  color: black;
  font-weight: bolder;
  transform: scale(1);
  background-color: #6f48eb33;
  background-color: #b1d9ff;
  color: white;
}

