/* 오늘날짜 */
.react-calendar__tile--now {
  background: #ff947a;
  border: 1px;
  background-color: #6f48eb33;
  font-weight: bolder;
  color: #4094bf;
  border-radius: 9999px;
  border: 1px solid #000;
}

